import React, { useCallback, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import { login, register, resetUserInfo, setRegisterInfo, setUserInfo } from "../../../store/userInfoSlice";

import TextInput from "../../../components/TextInput";
import Button from "../../../components/Button";
import AppBar from "../../../components/AppBar";
import Modal from "../../../components/Modal";

import { EyeIcon, EyeSlashIcon } from "@heroicons/react/24/outline";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../../store";
import { Email, Language } from "@mui/icons-material";
import { LoginResponse, NormalResponse } from "../../../type/userType";
import { useTranslation } from "react-i18next";
import { showCustomToast } from "../../../components/CustomToast";
import toast from "react-hot-toast";
import { getFontClass } from "../../../utils/helper";

interface RegisterFormProps {
  onSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
}

const RegisterForm: React.FC<RegisterFormProps> = ({ onSubmit }) => {
  const {t} = useTranslation();
  const dispatch:AppDispatch = useDispatch();
  const registerInfo = useSelector((state: RootState) => state.userInfo.registerInfo);
  const language = useSelector((state: RootState) => state.init.language);
  const navigate = useNavigate();

  const [showErrorModal, setShowErrorModal] = useState(false);
  const [registerValue, setRegisterValue] = useState({
    username: "",
    password: "",
    confirmPassword: "",
    email: "",
  });
  const [errors, setErrors] = useState({
    username: "",
    password: "",
    confirmPassword: "",
    email: "",
  });
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [running, setRunning] = useState(false);  
  const [loadingToastId, setLoadingToastId] = useState("");

  const validateInputs = () => {
    let isValid = true;
    const newErrors = {
      username: "",
      password: "",
      confirmPassword: "",
      email: "",
    };

    // 帳號驗證：6-10個英文數字
    if (!/^[a-zA-Z0-9]{6,10}$/.test(registerValue.username)) {
      newErrors.username = t("帳號需為6-10個英文數字");
      isValid = false;
    }

    // 密碼驗證：4-10個英文數字
    if (!/^[a-zA-Z0-9]{4,10}$/.test(registerValue.password)) {
      newErrors.password = t("密碼需為4-10個英文數字");
      isValid = false;
    }

    // 確認密碼驗證
    if (registerValue.confirmPassword !== registerValue.password) {
      newErrors.confirmPassword = t("密碼與確認密碼不相符");
      isValid = false;
    }

    // Email 格式驗證
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(registerValue.email)) {
      newErrors.email = t("電子郵件格式不正確");
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = () => {

    if (running) return;

    if (validateInputs()) {
      const registerParam = {
        username: registerValue.username,
        password: registerValue.password,
        cpassword: registerValue.confirmPassword
      };

      setRunning(true);
       setLoadingToastId(showCustomToast(t("註冊中..."), "loading"));

      dispatch(register(registerParam))

      .catch((error) => console.log(error))
      .finally(() => {
        setTimeout(() => {
          setRunning(false); // 3 秒後解除禁用
        }, 5000);
      });
    }
  };

  useEffect(() => {
    console.log(registerInfo);
    if (registerInfo) {
      if (registerInfo.code === 0) {
        const userData = { username: registerValue.username, password: registerValue.password };
        dispatch(login(userData))
        .then((result:any) => {
          const payload = result.payload
          if (payload && payload.username && payload.sid) {
            const restParam = {nmail: registerValue.email};
            dispatch(resetUserInfo(restParam))
            .then((result) => {
              const payload = result.payload as NormalResponse;
              if (payload.code === 200) {
                toast.dismiss(loadingToastId);
                navigate("/app/Login/RegistrationSuccessful");
              }
            })
            .catch((error) => {
              console.error('reset userInfo failed:', error);
            })
          }
        })
        .catch((error) => {
          console.error('login failed:', error);
        });

      } else {
        setRunning(false);
        toast.dismiss(loadingToastId);
        setShowErrorModal(true);
      }
    }
    console.log("registerInfo: ", registerInfo);
  }, [registerInfo]);

  return (
    <>
      <div className={`${getFontClass(`${language}_m`)} space-y-4 p-4`} onSubmit={handleSubmit}>
        <AppBar title={t("註冊")} />
        <div>
          <label className="block mb-2">{t('帳號')}</label>
          <TextInput
            placeholder={t("帳號：需6-10個英文數字")}
            value={registerValue.username}
            onInputChange={(value: any) =>
              setRegisterValue({ ...registerValue, username: value })
            }
            className={errors.username !== '' ? "border-red-500" : ""}
            autocomplete="username"
          />
          {errors.username && <p className="text-red-500 text-xs">{errors.username}</p>}
        </div>
        <div>
          <label className="flex mb-2 items-center">{t('密碼')}</label>
          <TextInput
            type={showPassword ? "text" : "password"}
            placeholder={t("密碼：需4-10個英文數字")}
            value={registerValue.password}
            onInputChange={(value: any) =>
              setRegisterValue({ ...registerValue, password: value })
            }
            className={errors.password !== '' ? "border-red-500" : ""}
            autocomplete="new-password"
            rightIcon={
              <EyeIconComponent show={showPassword} setShow={setShowPassword} />
            }
          />
          {errors.password && <p className="text-red-500 text-xs">{errors.password}</p>}
        </div>
        <div>
          <label className="flex mb-2 items-center">{t('確認密碼')}</label>
          <TextInput
            type={showConfirmPassword ? "text" : "password"}
            placeholder={t("請再次輸入密碼")}
            value={registerValue.confirmPassword}
            onInputChange={(value: any) =>
              setRegisterValue({ ...registerValue, confirmPassword: value })
            }
            className={errors.confirmPassword !== '' ? "border-red-500" : ""}
            autocomplete="new-password"
            rightIcon={
              <EyeIconComponent
                show={showConfirmPassword}
                setShow={setShowConfirmPassword}
              />
            }
          />
          {errors.confirmPassword && (
            <p className="text-red-500 text-xs">{errors.confirmPassword}</p>
          )}
        </div>
        <div>
          <label className="block mb-2">{t('電子郵件')}</label>
          <TextInput
            type="text"
            placeholder={t("請輸入Email")}
            value={registerValue.email}
            onInputChange={(value: any) =>
              setRegisterValue({ ...registerValue, email: value })
            }
            className={errors.email !== '' ? "border-red-500" : ""}
            autocomplete="email"
          />
          {errors.email && <p className="text-red-500 text-xs">{errors.email}</p>}
        </div>
        <div
          className="text-xs text-center"
          onClick={() => navigate("/app/Login/Terms")}
        >
          {t('點擊「註冊會員」表示您同意我們的')}
          <span className="text-red-500">{t('使用條款')}</span>
        </div>
        <Button className="button-full active mt-2" onClick={handleSubmit}>
          {t('免費註冊')}
        </Button>
        <Button className="button-full secondary mt-2" type="button" onClick={() => navigate("/app/Login")}>
          {t("登入")}
        </Button>
      </div>
      <Modal
        open={showErrorModal}
        onClose={() => {
          setShowErrorModal(false);
          dispatch(setRegisterInfo(null));
        }}
        title = {t('錯誤')}
        content={[<span>{registerInfo?.data?.data ?? ''}</span>]}
      />
    </>
  );
};

function EyeIconComponent({ show, setShow }: { show: boolean; setShow: (value: boolean) => void }) {
  return show ? (
    <EyeIcon
      className="w-6 h-6 text-gray-600 stroke-1.5 cursor-pointer"
      onClick={() => setShow(!show)}
    />
  ) : (
    <EyeSlashIcon
      className="w-6 h-6 text-gray-600 stroke-1.5 cursor-pointer"
      onClick={() => setShow(!show)}
    />
  );
}

export default RegisterForm;
